'use strict';

const headerBGImg = document.querySelector('.header__img-container');
const headerTitle = document.querySelector('.header__title');
const headerText = document.querySelector('.header__text');
const headerRtCol = document.querySelector('.header__right-column');
const bakeryContMore = document.querySelector('.bakery__container-more');
const bakeryButton = document.querySelector('.bakery__button');
const burger = document.querySelector('.nav__burger');
const callbackTop = document.querySelector('.callback__top');
const callback = document.querySelector('.callback');
const navList = document.querySelector('.right-nav');
const burgerLine = document.querySelector('.nav__burger-line');
const toggler = document.querySelector('#toggler');
const togglerBGImgNext = document.querySelector('#togglerBGImgNext');
const togglerBGImgPrev = document.querySelector('#togglerBGImgPrev');
const mobBGImgNext = document.querySelector('#mobBGImgNext');
const mobBGImgPrev = document.querySelector('#mobBGImgPrev');
const toogleBakeryContMore = document.querySelector('#bkMore');
const numOne = document.querySelector('.image-change__num');
const numTwo = document.querySelector('.image-change__num2');
const numThree = document.querySelector('.image-change__num3');
const numOneCopy = document.querySelector('.image-change__numCopy');
const numTwoCopy = document.querySelector('.image-change__num2Copy');
const numThreeCopy = document.querySelector('.image-change__num3Copy');

let currentBGIMG = 1;

mobBGImgNext.addEventListener('click', () => {
  if (currentBGIMG >= 3) {
    currentBGIMG = 0;
  }

  currentBGIMG++;
  headerBGImg.classList.add(`header__img-container--${currentBGIMG}`);

  if (currentBGIMG === 1) {
    numOne.classList.add(`image-change__num--${currentBGIMG}`);
  }

  if (currentBGIMG === 2) {
    numTwo.classList.add(`image-change__num2--${currentBGIMG}`);
  }

  if (currentBGIMG === 3) {
    numThree.classList.add(`image-change__num3--${currentBGIMG}`);
  }

  const noCurrentNext = currentBGIMG + 1;
  let noCurrentPrev = currentBGIMG - 1;

  if (currentBGIMG === 1) {
    noCurrentPrev = 3;
  }

  if (noCurrentNext !== currentBGIMG) {
    numOne.classList.remove(`image-change__num--${noCurrentNext}`);
    numOne.classList.remove(`image-change__num--${noCurrentPrev}`);
    numTwo.classList.remove(`image-change__num2--${noCurrentNext}`);
    numTwo.classList.remove(`image-change__num2--${noCurrentPrev}`);
    numThree.classList.remove(`image-change__num3--${noCurrentNext}`);
    numThree.classList.remove(`image-change__num3--${noCurrentPrev}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentNext}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentPrev}`);
  }

  if (currentBGIMG === 3) {
    currentBGIMG = 0;
  }
});

mobBGImgPrev.addEventListener('click', () => {
  currentBGIMG--;

  if (currentBGIMG <= 0) {
    currentBGIMG = 3;
  }

  headerBGImg.classList.add(`header__img-container--${currentBGIMG}`);

  if (currentBGIMG === 1) {
    numOne.classList.add(`image-change__num--${currentBGIMG}`);
  }

  if (currentBGIMG === 2) {
    numTwo.classList.add(`image-change__num2--${currentBGIMG}`);
  }

  if (currentBGIMG === 3) {
    numThree.classList.add(`image-change__num3--${currentBGIMG}`);
  }

  let noCurrentNext = currentBGIMG + 1;
  const noCurrentPrev = currentBGIMG - 1;

  if (currentBGIMG === 3) {
    noCurrentNext = 1;
  }

  if (noCurrentNext !== currentBGIMG) {
    numOne.classList.remove(`image-change__num--${noCurrentNext}`);
    numOne.classList.remove(`image-change__num--${noCurrentPrev}`);
    numTwo.classList.remove(`image-change__num2--${noCurrentNext}`);
    numTwo.classList.remove(`image-change__num2--${noCurrentPrev}`);
    numThree.classList.remove(`image-change__num3--${noCurrentNext}`);
    numThree.classList.remove(`image-change__num3--${noCurrentPrev}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentNext}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentPrev}`);
  }
});

togglerBGImgNext.addEventListener('click', () => {
  if (currentBGIMG >= 3) {
    currentBGIMG = 0;
  }

  currentBGIMG++;
  headerBGImg.classList.add(`header__img-container--${currentBGIMG}`);

  if (currentBGIMG === 1) {
    numOneCopy.classList.add(`image-change__numCopy--${currentBGIMG}`);
  }

  if (currentBGIMG === 2) {
    numTwoCopy.classList.add(`image-change__num2Copy--${currentBGIMG}`);
  }

  if (currentBGIMG === 3) {
    numThreeCopy.classList.add(`image-change__num3Copy--${currentBGIMG}`);
  }

  const noCurrentNext = currentBGIMG + 1;
  let noCurrentPrev = currentBGIMG - 1;

  if (currentBGIMG === 1) {
    noCurrentPrev = 3;
  }

  if (noCurrentNext !== currentBGIMG) {
    numOneCopy.classList.remove(`image-change__numCopy--${noCurrentNext}`);
    numOneCopy.classList.remove(`image-change__numCopy--${noCurrentPrev}`);
    numTwoCopy.classList.remove(`image-change__num2Copy--${noCurrentNext}`);
    numTwoCopy.classList.remove(`image-change__num2Copy--${noCurrentPrev}`);
    numThreeCopy.classList.remove(`image-change__num3Copy--${noCurrentNext}`);
    numThreeCopy.classList.remove(`image-change__num3Copy--${noCurrentPrev}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentNext}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentPrev}`);
  }

  if (currentBGIMG === 3) {
    currentBGIMG = 0;
  }
});

togglerBGImgPrev.addEventListener('click', () => {
  currentBGIMG--;

  if (currentBGIMG <= 0) {
    currentBGIMG = 3;
  }

  headerBGImg.classList.add(`header__img-container--${currentBGIMG}`);

  if (currentBGIMG === 1) {
    numOneCopy.classList.add(`image-change__numCopy--${currentBGIMG}`);
  }

  if (currentBGIMG === 2) {
    numTwoCopy.classList.add(`image-change__num2Copy--${currentBGIMG}`);
  }

  if (currentBGIMG === 3) {
    numThreeCopy.classList.add(`image-change__num3Copy--${currentBGIMG}`);
  }

  let noCurrentNext = currentBGIMG + 1;
  const noCurrentPrev = currentBGIMG - 1;

  if (currentBGIMG === 3) {
    noCurrentNext = 1;
  }

  if (noCurrentNext !== currentBGIMG) {
    numOneCopy.classList.remove(`image-change__numCopy--${noCurrentNext}`);
    numOneCopy.classList.remove(`image-change__numCopy--${noCurrentPrev}`);
    numTwoCopy.classList.remove(`image-change__num2Copy--${noCurrentNext}`);
    numTwoCopy.classList.remove(`image-change__num2Copy--${noCurrentPrev}`);
    numThreeCopy.classList.remove(`image-change__num3Copy--${noCurrentNext}`);
    numThreeCopy.classList.remove(`image-change__num3Copy--${noCurrentPrev}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentNext}`);
    headerBGImg.classList.remove(`header__img-container--${noCurrentPrev}`);
  }
});

toogleBakeryContMore.addEventListener('click', () => {
  bakeryContMore.classList.toggle('bakery__container-more--active');
  bakeryButton.classList.toggle('bakery__button--active');
});

toggler.addEventListener('click', () => {
  headerTitle.classList.toggle('header__title--active');
  headerText.classList.toggle('header__text--active');
  headerRtCol.classList.toggle('header__right-column--active');

  callbackTop.classList.toggle('callback__top--active');
  callback.classList.toggle('callback--active');

  navList.classList.toggle('right-nav--active');

  burger.classList.toggle('nav__burger--active');
  burgerLine.classList.toggle('nav__burger-line--active');
});
